<template>
  <div>
    <div class="row no-gutters widget-2 shadow-base">
      <div class="col-sm-12 col-lg-12">
        <div class="card">
           <div class="card-header bd-b-color-gray-lighter">
               <h6 class="card-title"><i class="ion-ios-paper-outline tx-18 va-m"></i>   </h6>
               
            </div>
             <div class="card-body">
                <div class="row tx-center">
                    <div class="col-lg-8">
                         <div class="card-header pd-0 mg-b-10 btn-group-xs bd-0 ">
                             <h6 class="tx-14 tx-info tx-left"><i class="ion-android-arrow-dropright-circle tx-14"></i>  </h6>
                         </div>
                         <!-- <button type="button" v-on:click="selectMsgList" >click</button> -->
                         <div class="table-wrap">
                          <div class="table-wrap-header pd-t-3 w-m-1">
                              <div class="btn-group-xs text-left pd-l-5"><button v-on:click="onClickDeleteMsgdata" type="button" class="btn btn-outline-secondary"><i class="ion-ios-plus text-minus tx-14"></i>  </button></div>
                          </div>   
                          <table class="table table-bordered table-mid tx-13 tx-gray-700 mg-b-0 bd">
                            <thead>
                            
                            <tr class="bg-gray-100 tx-11 tx-uppercase tx-gray-800">
                                <th class="wd-5p tx-center"></th>
                                <th class="wd-10p tx-center"></th>
                                <th class="wd-10p tx-center"></th>
                                <th class="wd-20p tx-center"></th>
                                <th class="wd-10p tx-center"></th>
                                <th class="wd-45p tx-center"></th>
                            </tr>
                            </thead>
                            <tbody >
                              <tr v-if="msgDatas.length == 0 ">
                                <td colspan="6">{{noData}} </td>
                              </tr>
                              <tr v-for="item in msgDatas" v-bind:key="item.seq"> 
                               <td><input type="checkbox" v-bind:value="item.seq" v-on:change="getCheck($event, item)"></td>
                               <td>{{item.create_date}}</td>
                               <td>{{item.from_user_id}}</td>
                               <td>{{item.prj_id}}</td>
                               <td>{{item.app_type}}</td>
                               <td>{{item.message_content}}</td>
                              </tr> 
                             <!-- <tr>
                               <td><input type="checkbox"></td>
                               <td>2021-04-15</td>
                               <td></td>
                               <td> 1</td>
                               <td></td>
                               <td>  .   100      .</td>
                              </tr> -->
                              
                            </tbody>
                          </table>  
                            <div class="table-wrap-bottom"></div>    
                         </div>                                 
                    </div>

                    <div class="col-lg-4">
                      <!--   -->
                         <div class="card-header pd-0 mg-b-10 btn-group-xs bd-0 ">
                             <h6 class="tx-14 tx-info tx-left"><i class="ion-android-arrow-dropright-circle tx-14"></i>  </h6>
                         </div>
                          <div class="form-group text-left">
                            <label class="form-control-label">: <span class="tx-danger">*</span></label>
                            <input class="form-control  form-control-sm form-input" type="text" name="firstname"  placeholder=" ." readonly
                          v-model="passObj.selectedUser"
                          v-on:click="$refs.userListtModal.showUserList()" />
                          <!-- v-model="userID"-->
                          <!-- @input="getContent" -->
                         </div>
                         <div>
                             <div id="xp-summernote"></div>

                         </div>
                         <div class="btn-group-xs pd-t-10 text-right">
                             <button type="button" class="btn btn-primary" v-on:click="clickCreate()"></button>
                         </div>
                    </div>

                </div>
             </div>
        </div>
      </div>

    </div>
   
  </div>
</template>
<script>
import Axious from 'axios'
export default {
    data:function(){
        return {
            passObj:{
                selectedUser:'',
            },
            msgDatas : [],   
            userList:[],
            checkedList:[],
            noData:"  .",
            // 
           userID : '',
           msgcontent:''
        }

    },
    mounted:function(){
       $("#xp-summernote").summernote({
        toolbar: [
        ['style', ['bold', 'italic', 'underline', 'clear']],
        ],
        height: 150,
        minHeight: null,
        maxHeight: null,
        focus: false,
        lang: "ko-KR",
      })
      //  
      this.selectMsgList();
      //   - 
     // this.showUserList()
      
    },

    methods: { 
      selectMsgList(){
        let ob = this;
        Axious.get("/msgDir/messages/list")
        .then(function(response){
          ob.msgDatas=response.data.msgList;
         
        })
        .catch(function(err){
          
        });
      },
      showUserList(){
        let obj = this;
        Axious.get("")
        .then(function(response) {
         // obj.userList = response.data; 
        })
        .catch(function(err) {
          
        })

      },
       getCheck(event,paramObj){

          let isChecked=event.target.checked;
          let _index=this.checkedList.indexOf(paramObj.seq);
          if(isChecked == true &&  _index < 0 ){
            this.checkedList.push(paramObj.seq);
      
          }else{
              this.checkedList.splice(_index,1);
          }
       },

        onClickDeleteMsgdata(){
        let obj = this;
        if(this.checkedList.length == 0){
          alert("  .");
          return false;
        }
        Axious.post("/msgDir/Message/delete",{listSeq:this.checkedList})
         .then(function(response){

            if(response.data.resultCount >0 ){      
            alert(" .");
            obj.selectMsgList();
            
            }else{
              alert("");
            }
         })
         .catch(function(err){
           console.log(err);
         });
      },
      //  
      clickCreate(){
        let obj = this ;

       let contents = {
          to_user_id : this.userID,
          message_content: $("#xp-summernote").summernote("code") 
         
       };
        if(this.userID == null || this.userID ==''){
          alert('  ');
          return false;
        }
        Axious.post("/msgDir/Message/create", {messageDTO:contents })
        .then(function(response){
            if(response.data.resultCount > 0){
              alert('');
              obj.userID='';
              $("#xp-summernote").summernote("code","") ;
            
            }
        })
        .catch(function(error){
        });
      }
    }
}
</script>


